
import XHeader from 'vux/src/components/x-header/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Divider from 'vux/src/components/divider/index.vue'

export default {
components: { XHeader, Tab, TabItem,Swiper,SwiperItem,Divider },
data() {
return {
index:0
}
},
methods: {
onItemClick(index){
this.index = index;
},
onIndexChange(index){
var tableItems = [].slice.call(document.querySelectorAll('.vux-tab-item'));
tableItems[index].click();
}
},
mounted() {

}
}




